	module.exports = {"data":{"markdownRemark":{"html":"<h2>Link</h2>\n<p>The <code>:link</code> CSS pseudo-class is used to target all unvisited elements (e.g. anchor <code>&#x3C;a></code> elements) that have an <code>href</code> attribute, even if the <code>href</code> has an empty value.</p>\n<p>General Syntax:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token property\">target</span><span class=\"token punctuation\">:</span>link\n</code></pre>\n      </div>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token comment\" spellcheck=\"true\">/* Selects any &lt;a> that has not been visited yet */</span>\n<span class=\"token selector\">a:link</span> <span class=\"token punctuation\">{</span> \n    <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> red<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/:link\">:link (MDN)</a></li>\n</ul>","fields":{"slug":"/css/selectors/pseudo/link/"},"frontmatter":{"title":"Link"}}},"pathContext":{"slug":"/css/selectors/pseudo/link/"}}

